.contact-box {
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 4px;
}
.contact-box .icon {
    width: 100px;
    height: 100px;
    background: #3e64ff;
    margin: 0 auto;
    margin-bottom: 10px;
    border-radius: 50%;
}
.contact-box .icon span {
    color: #fff;
}
.contact-box h3 {
    font-weight: 600;
    color: #000;
}
.block-20 {
    overflow: hidden;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position: relative;
    display: block;
    width: 100%;
    height: 300px;
}
.blog-entry {
    position: relative;
    height: 100%;
}
@media (min-width: 768px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
@media (max-width: 767.98px) {
    .blog-entry {
        margin-bottom: 30px;
    }
}
.blog-entry .text {
    width: 100%;
    margin: 0 auto;
}
.blog-entry .text .heading {
    font-size: 20px;
    margin-bottom: 16px;
    font-weight: 500;
}
.blog-entry .text .heading a {
    color: #000000;
    text-decoration: none;
}
.blog-entry .text .heading a:hover, .blog-entry .text .heading a:focus, .blog-entry .text .heading a:active {
    color: #3e64ff;
}
.blog-entry .meta {
    display: inline-block;
    margin-right: 5px;
    margin-bottom: 0;
    font-size: 16px;
    font-weight: 600;
}
.blog-entry .meta span {
    color: #3e64ff;
}
.blog-entry .meta a {
    color: #3e64ff;
}
.blog-entry .meta a:hover {
    color: #3e64ff;
}
.hire-me {
    background-image: url(/static/media/bg_1.f7de43c6.jpg);
    /*background: #3e64ff;*/
    z-index: 0;
    position: relative;
    padding: 120px;
    margin: 0;
    color: #fff;
}
.hire-me h2 > span {
	color: #9FF569;
}
.hire-me .overlay {
	position: absolute;
	content: '';
	background: #3e64ff;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: .7;
}
.hire-me a.btn {
	border-radius: 100px;
}
